.wrap-content.promotion-words{
  padding-top: 158px;
  .wrap-item{
    .item-list{
      display: flex;
      justify-content: space-between;
      flex-direction: row;
      flex-wrap: wrap;

      li{
        // border: 1px solid black;
        width: 30.556%;
        
        &:not(:nth-child(n+6)){
          margin-bottom: 126px;
        }
        .wrap-img{
          border-radius: 10px;
          overflow: hidden;
          height: 260px;
        }
        
        strong{
          font-size: 20px;
          margin-top: 20px;
          display: inline-block;
          margin-bottom: 17px;
          color: #3c3c3c;
        }

        .ico-new{
          background-color: #2C99FF;
          border-radius: 50%;
          width: 21px;
          height: 21px;
          display: inline-block;
          // background-image: url('../../public/assets/images/ico-new.png');
          background-image: url('../../../../public/asset/images/ico-new.png');
          background-repeat: no-repeat;
          background-position: center;
          position: relative;
          top: 3px;
          margin-left: 6px;
        }
      }
    }

    .pagination{
      // 임의로 영역 지정
      width: 471px;
      height: 30px;
      margin: 0 auto;
      background-color: black;
      margin-top: 148px;
      margin-bottom: 96px;
    }
  }
}