//color 찾아서 따로 변수에 빼주세요 
.wrap-content.promotion-overview{
    background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
    border-radius: 50px 50px 0 0;
    
    .video{
        padding: 96px 0;
        
        .title{
            text-align: center;
            color: #fff;
            font-weight: bold;
            font-size: 36px;
        }

        .media-list{
            margin-top: 96px;
            display: flex;
            justify-content: space-between;

            li{
                background-color: #fff;
                border-radius: 10px;
                overflow: hidden;
                width: 30.556%;
                
                .item-card{
                    display: flex;
                    flex-direction: column;

                    .wrap-img{
                        height: 230px;
                        overflow: hidden;

                        img{
                        }
                    }

                    .content{
                        padding: 30px;
                        position: relative;

                        strong{
                            display: block;
                            font-size: 20px;
                            padding-bottom: 20px;
                        }

                        .icon-play{
                            width: 39px;
                            height: 33px;
                            background-color: black;
                            display: inline-block;
                            position: absolute;
                            right: 37px;
                            bottom: 32px;
                        }
                    }
                }
            }
        }

        .a-view{
            color: #fff;
            font-size: 18px;
            margin-top: 76px;
            display: flex;
            align-items: center;
            justify-content: center;

            img{
                width: 14px;
                height: 7px;
                margin-left: 7px;
            }
        }
    }
}