// 🏠️ OSP-3111
@import '../../modules';
@import '../../layout/board';

section.main-banner{
  position: relative;
  height: 896px;
}

.banner-slider{
  height: 100%;
  background: url('../../../../public/asset/images/main_banner.png');
  background-position: left;
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;

  .slider-content{
    position: relative;
    height: 100%;
    padding-left: 5.4rem;

    height: calc(345px + 27px);
    color: $font_color-white;

    h1{
      padding-top: calc(19.2rem + 8.4rem);
      @include style_font(700, 5.2rem, 1.6em, $font_color-white);
      margin-bottom: 16px;
    }
    p{
      line-height: 2em;
      padding-bottom: 3.5rem;
    }
    div.slider-controller{
      button, span{
        color: white;
      }
      .page-now{
        font-weight: 700;
      }
      .page-total{
        color: rgba($font_color-black, $alpha: 0.5);
      }
    }
  }
}
.banner-notice{
  position: absolute;
  bottom: 0;
  width: 100%;

  .width1440{
    display: flex;
    align-items: center;
    height: 73px;
    padding: 1em 20px;
    
    background-color: $bg-main-notice;
    border-radius: 1em 1em 0 0;
  }
  span{
    width: 108px;
    // flex: 1 1 auto;
    color: $font_color-white;
    padding: 5px 20px;
  }
  a{
    color: $font_color-white;
  }
  .notice-now{
    width: calc(100% - 258px);
    // flex: 50 10 auto;
    padding: 1em;
    font-weight: 400;
  }
  .notice-all{
    padding: 1em;

    width: 150px;
    text-align: right;
    font-weight: 500;
    // flex: 1 1 auto;
  }
}

// ---------------------------------
main.main-osp3111{
  section.main-product{
    text-align: center;
    .width1440{
      height: 1025px;

      padding: 9.6rem 0;

      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
    }
    background-color: $color-2C99FF_azure;
    
    @include style_main_h3($font_color-white);

    div.product-cards{
      width: 100%;
      display: flex;
      justify-content: space-between;
      button{
        width: 462px;
        height: 645px;
        padding: 45px 0;

        display: flex;
        flex-direction: column;
        align-items: center;

        background: #FFFFFF;
        box-shadow: 0px 20px 20px rgba(0, 0, 0, 0.08);
        border-radius: 3rem;
        img{
          display: block;
          // margin: 0 auto;
          width: 270px;
          height: 270px;
          background-color: aliceblue;
        }
        h4{
          font-size: 32px;
          font-weight: 700;
          margin: 64px 0 36px 0;
        }
        p{
          width: 382px;
          height: 81px;
          font-size: 19px;
          line-height: 150%;
          color: $font_color-848484_grey;
        }
        span{
          margin-top: 33px;
        }
      }
    }
  }

  



}
