@import './../modules';

// *subpage에 banner - basic
section.subpage-banner{
  padding-top: $number_nav_height-84;
  position: relative;

  color: $font_color-white;
  background: linear-gradient(90.72deg, #4661FF 0.62%, #4BA5FF 99.38%);

  div.gnb-depth2{
    display: block;
    position: relative;
    background: rgba(52, 92, 224, 0.18);
    ul.width1440{
      display: flex;
    }
    a{
      display: inline-block;
      padding: 22px 5px 24px 5px;
      margin: 0 1em;
      font-weight: 500;
      color: rgba(255, 255, 255, 0.6);
    }
    .tab-focused, a:hover{
      @include style_font(700, 1.8rem, 1, white);
      transition: all 0.3s;
    }
  }
  div.width1440{
    padding: 10rem 1rem 4.6rem 1rem;
    @include animation(0s, 0.6s, moveUp);
    h2{
      padding-bottom: 3.2rem;
      @include style_font(700, 3.8rem, 1, white);
    }
    p{
      height: 10rem;
      letter-spacing: 0;
      @include style_font(400, 2.4rem, 1.8em, white);
      span{
        display: block;
      }
    }
  }
}

// *subpage에 banner - community
section.community{
  background: linear-gradient(90.72deg, #00BAE2 0.62%, #00E0D3 99.38%);
}

// * tab style1
div.tab-style_box1{
  display: flex;
  justify-content: center;
  align-items: center;

  padding-top: 9.6rem;
  padding-bottom: 3.2rem;
  ul{
    position: relative;
    display: flex;
    &::before{
      // 그냥 border주면 안맞음
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;

      border-radius: 1rem;
      border: 3px solid $line_color-ECECEC_grey;
      z-index: -9;
    }
    li{
      width: 29.3rem;
      text-align: center;
      // padding: 0 1.4rem;
      button{
        @include style_font(700, 1.8rem, 1, $font_color-AFAFAF_grey);
        padding: 2rem 8rem;
      }
      &.--active{
        left: 10%;
        height: 60px;
        background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
        border-radius: 1rem;
        button{
          color: white;
        }
      }
    } 
  }
  .tab-focus{
    width: 33.33%;
    transform: translateX(0);
  }
}

// * tab style2
div.tab-style_box2{
  position: relative;
  &::after{
    position: absolute;
    content: '';
    bottom: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background: #ececec;
  }
  ul.width1440{
    display: flex;
    justify-content: center;
    li{
      text-align: center;
    }
    a, button{
      position: relative;
      display: inline-block;
      font-weight: 700;
      font-size: 20px;
      width: 220px;
      padding: 5.2rem 1em 3.8rem 1em;
      color: $font_color-AFAFAF_grey;
      
      &::after{
        position: absolute;
        content: '';
        bottom: 0;
        left: 0;
        width: 100%;
        height: 5px;
        background: $color-2C99FF_azure;
        z-index: 99;
        opacity: 0;
      }
    }
    .tab-focused, button:hover{
      color: $color-2C99FF_azure;  
      transition: all 0.3s;

      &::after{
        opacity: 1;
      }
    }
  }
}
div.tab-style_box2.community{
  ul.width1440{
    a, button{
      &::after{
        background: $color-0AB4E2_commu-01;
      }
    }
    .tab-focused, button:hover{
      color: $color-0AB4E2_commu-01;  
    }
  }
}