// 🏠️ OSP-5112

@import '../../modules';

main.main-osp5112{
  position: relative;


  section > h2{
    padding-bottom: 8.4rem;
    text-align: center;
    @include style_font(700, 4rem, 1, $font_color-212121_grey)
  }
  section.tmaxgr-feature--main{
    padding-top: 10rem;
    padding-bottom: 10rem;
    text-align: center;
    h1{
      font-weight: 700;
      font-size: 48px;
      padding-bottom: 4.7rem;
    }
    > p{
      width: 927px;
      margin: 0 auto;
      font-size: 32px;
      color: #666666;
    }
    span.img_tmaxgr-feature{
      display: inline-block;
      width: 1359px;
      height: 880px;
      background-color: rgba($color: #000000, $alpha: 0.2);
    }
    ul.feautre--main-list{
      display: flex;
      justify-content: center;
      padding-top: 9.5rem;
      li{
        width: 40.2rem;
        padding: 0 3rem;
        i.icon-tmaxgr-feature{
          width: 9rem;
          height: 9rem;
          background-color: #4661FF;
        }
        h5{
          @include style_font(700, 2.8rem, 1, $font_color-212121_grey);
          padding-top: 4.9rem;
          padding-bottom: 3.2rem;
        }p{
          @include style_font(400, 18px, 1.6em, $font_color-212121_grey);
        }
      }
    }
  }
  section.tmaxgr-feature--info{
    // 디자인 확정나면 작업하기
    height: 1828px;
    background: $bg_main-F8F9FA;
  }
  section.tmaxgr-feature--business{
    padding-top: 15rem;
    ul.business-list{
      background: $bg_main-F8F9FA;
      border-radius: 80px;
      display: flex;
      li{
        padding: 6.4rem 5em 7.4rem 5em;
        flex: 33.33%;
        text-align: center;
        i.icon-tmaxgr-business{
          width: 18rem;
          height: 18rem;
          background-color: white;
          border-radius: 50%;
        }
        h6{
          @include style_font(700, 3.2rem, 1, $color-2C99FF_azure);
          padding-top: 5.9rem;
          padding-bottom: 3rem;
          @include make_line(20px, 4px, $color-2C99FF_azure)
        }
        p{
          padding-top: 3rem;
          @include style_font(400, 2.2rem, 1.6em, #666666);

        }
      }
    }
  }
  section.tmaxgr-feature--model{
    padding-top: 13.2rem;
    padding-bottom: 10.3rem;
    ul.model-list{
      display: flex;
      justify-content: space-between;
      li{
        text-align: center;
        width: 70rem;
        // background: #FFFFFF;
        box-shadow: 0px 7px 30px rgba(44, 153, 255, 0.1);
        border-radius: 50px;
        h6{
          padding: 3rem 0;
          @include style_font(700, 2.8rem, 1, white);
        }
        span.img_tmaxgr-model{
          width: 100%;
          height: 51.6rem;
          border-radius: 0 0 50px 50px;
          background-color: #4661FF;
        }
        &:first-child{
          // background-color: #838A92;
          background: linear-gradient(to bottom, #838A92 15%, white 15%);
          
        }
        &:last-child{
          background: linear-gradient(to bottom, $color-2C99FF_azure 15%, white 15%);

          h6{
          }
        }
      }
      
    }
  }
  section.tmaxgr-feature--tbd{
    height: 1681px;
    background: #C4C4C4;
  }
}