// 🏠️ OSP-4116

@import '../../modules';

main.main-osp4116{
  strong{
    color: $color-2C99FF_azure;
  }

  h1{
    @include style_font(700, 3.6rem, 1, #1F1F1F);
    padding-top: 6.4rem;
    text-align: center;
    strong{
      display: block;
      padding-top: 2rem;
    }
  }
  section.history{
    position: relative;
    padding-bottom: 23.7rem;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    div:first-child{
      padding-top: 16rem;
      &::before{
        display: none;
      }
    }
    > div{
      position: relative;
      padding-top: 28.8rem;
      &::before{
        position: absolute;
        content: '';
        display: inline-block;

        text-align: center;
        right: 12.8rem;
        bottom: 28rem;
        // right: -50%;
        width: 2.3rem;
        height: 28rem;

        background-color: blue;
        opacity: 0.3;

        // transform: translateX(100%);
      }

      span.img_history{
        position: relative;
        display: block;
        width: 28rem;
        height: 28rem;
        background: $bg_main-F8F9FA;
        border-radius: 50%;
        &::after{
          position: absolute;
          content: '';
          width: 24rem;
          height: 24rem;
          background-color: red;
        }
      }
      div.history-content{
        position: absolute;
        width: 38.4rem;
        bottom: -10%;

        h3{
          @include style_font(700, 3.4rem, 1, $font_color-212121_grey);
          padding-bottom: 5.2rem;
        }
        ul{
          li{
            padding-bottom: 4.4rem;
            p{
              @include style_font(400, 2.4rem, 1, #666);
              strong{
                @include style_font(700, 3.2rem, 1, $color-2C99FF_azure);
              }
            }
          }
          li:last-child{
            padding-bottom: 0;
          }
        }
      }
    }

// 방향에따라 바뀜
    div.--right{
      span.img_history{
        &::after{
          right: 9.8rem;
          bottom: 7.2rem;
        }
      }
      div.history-content{
        right: -31rem;
        strong{
          padding-right: 1.6rem;
        }
      }
    }
    div.--left{
      span.img_history{
        &::after{
          left: 9.8rem;
          bottom: 7.2rem;
        }
      }
      div.history-content{
        text-align: right;
        left: -31rem;
        strong{
          padding-left: 1.6rem;
        }
      }
    }
  }
}