/**
** Board - Board - section.main-board
** Board - News - section.main-news
** Board - Customer - section.main-customer
*/

section.main-board{
  .width1440{
    height: 815px;
    text-align: center;
    padding: 9.5rem 0;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  @include style_main_h3($font_color-black);
  div.board-cards{
    display: flex;
    height: 320px;
    justify-content: space-between;
    button{
      position: relative;
      width: 335px;

      display: flex;
      flex-direction: column;
      justify-content: space-between;

      text-align: left;
      img{
        width: 100%;
        height: 195px;
        border-radius: 1rem;

        background-color: dimgray;
      }
      .board-title{
        font-size: 20px;
        font-weight: 500;
        line-height: 150%;
        color: #000000;
      }
      .board-date{
        font-size: 18px;
        line-height: 150%;
        
        color: #B0B0B0;
      }
    }
  }
}

section.main-news{
  @include style_main_h3($font_color-black);

  background: $bg_main-news;
  .width1440{
    padding: 96px 0;
    height: 1128px;
    
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  div.news-grid{
    position: relative;
    height: 683px;

    display: grid;
    grid-template-columns: 830px 577px;

    column-gap: 3.2rem;
    row-gap: 3rem;
    button{
      text-align: left;
      border-radius: 10px;
      background-color: white;
      .news-content{
        padding: 4rem;
        .news-title{
          height: 77px;
          font-weight: 500;
          font-size: 24px;
          line-height: 150%;
          color: $font_color-black;
          margin-bottom: 20px;

          // 두 줄부터 ... 해야해용
          // white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
        .news-date{
          line-height: 170%;
          color: $font_color-B0B0B0_grey;
          margin-top: 14px;
          font-weight: 300;
        }        
      }
    }
    .--col1{
      display: flex;
      flex-direction: column;
      justify-content: space-between;        
      height: 100%;
      grid-row-start: 1;
      grid-row-end: 4;
      img{
        width: 830px;
        height: 439px;
        border-radius: 10px;
        background-color: darkgray;
      }
      .news-content{
        position: relative;
        height: 244px;
        padding: 4rem;
        p{
          line-height: 170%;
          width: 100%;
          letter-spacing: -0.5px;
        }
        .news-title{
          height: 55px;
          font-weight: 700;
          font-size: 26px;
          color: $font_color-black;
          margin-bottom: 5px;
        }
      }
    }
  }
}

section.main-customer{
  .width1440{
    position: relative;
    height: 35.7rem;
  }
  .customer-banner{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    padding: 7rem 16rem;
    .customer-title{
      padding: 1em 8.6rem;
      color: $font_color-white;
      h5{
        font-weight: 700;
        font-size: 36px;
        padding-bottom: 2.4rem;
      }
      p{
        font-weight: 400;
        font-size: 24px;
      }
    }
    .btn-customer{
      width: 280px;
      height: 76px;

      background: #FFFFFF;
      border-radius: 48px;
      color: $color-2C99FF_azure;
      font-weight: 700;
      font-size: 24px;
    }
  }
  .--banner{
    position: absolute;
    bottom: 0;      
    
    width: 100%;
    height: 299px;
    background: linear-gradient(90.72deg, #4661FF 0.62%, #4BA5FF 99.38%);
    border-radius: 30px;
  }
}
