// 🏠️ OSP-4113

@import '../../modules';

main.main-osp4113{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 240px;
  
  section{
    padding-top: 123px;
    display: flex;
    padding-bottom: 270px;
    div{
      padding-top: 14.7rem;
      h3{
        font-weight: 900;
        font-size: 32px;
        color: $color-2C99FF_azure;
        strong{
          font-size: 72px;
        }
      }
      h2{
        padding-top: 1.4rem;
        padding-bottom: 2.8rem;
        padding-right: 23.8rem;
        font-weight: 700;
        font-size: 40px;
        line-height: 1.4em;
      }
    }
    span.img_tmaxos-1{
      &::before{
        position: absolute;
        content: '';
        top: 0;
        left: -50%;
        width: 100%;
        height: 100%;
        background-color: $bg_main-news;
        border-radius: 50%;
        z-index: -9;
      }
      position: relative;
      display: block;
      width: 683px;
      height: 683px;
      background-color: aquamarine;
      border-radius: 50%;
    }
  }
  div.img_tmaxos-group{
    width: 1183px;
    height: 424px;
    background-color: gray;
  }
}