// icon을 mixin형태로 저장해두기 

// * 📔 Common rule - buttons

@mixin button_set($category){
  // btn-rounded-02 
  @if $category == 'basic-rounded-02'{
    display: flex;
    align-items: center;
    padding: 1.5rem 3rem;

    background: white;
    border-radius: 4rem;
    @include style_font(700, 2.2rem, 1, $color-2C99FF_azure);
    transition: all 0.3s;
    &:hover{
      color: $color-007DF1_azure-emp-01;
      background: white;
    }
    &:active{
      color: $color-0063C0_azure-emp-02;
    }
  }@else if $category == 'commu-rounded-02'{
    padding: 1.5rem 3rem;

    background: white;
    border-radius: 4rem;
    @include style_font(700, 2.2rem, 1, $color-2C99FF_azure);
    transition: all 0.3s;
    &:hover{
      color: $color-007DF1_azure-emp-01;
      background: white;
    }
    &:active{
      color: $color-0063C0_azure-emp-02;
    }
  }
}