// 🏠️ OSP-4112

@import '../../modules';

main.main-osp4114{
  text-align: center;
  background-color: $bg_main-news;
  section.container{
    padding-top: 9.6rem;
    padding-bottom: 20.5rem;
    h2{
      @include style_font(700, 3.6rem, 1, #1f1f1f);
      padding-bottom: 9.4rem;
    }
    ul{
      display: flex;
      gap: 8rem;
      justify-content: center;
      li{
        padding: 6.4rem 7rem 8.7rem 7rem;

        background: white;
        box-shadow: 0px 3rem 5rem rgba(175, 175, 175, 0.1);
        border-radius: 3rem;
        button{
          @include style_font(500, 2.4rem, 1, $font_color-212121_grey);
          display: block;
          width: 100%;
        }
      }
    }
  }
  div.toggle-container{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);

    display: flex;
    justify-content: center;
    align-items: center;
    div.img-toggle{
      width: 88.8rem;
      box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
      div.toggle-control{
        display: flex;
        justify-content: space-between;
        padding: 1.5rem 3.4rem;
        background-color: $color-2C99FF_azure;

        border-radius: 2rem 2rem 0 0;
        span{
          @include style_font(700, 2rem, 1.5em, white);
        }
      }
      div.toggle-img{
        height: 68rem;
        padding: 2rem 2rem 0 2rem;
        background: #F8F9FA;
        border-radius: 0 0 2rem 2rem;
        overflow: scroll;
        
        .img-certificate{
          background-color: white;
          background: linear-gradient(to top, rgba(2,0,36,1) 0%, rgba(0,212,255,1) 100%);
          border: 1px solid rgba(0, 0, 0, 0.1);
          width: 100%;
          height: 80rem;
        }
      }
    }

  }
}

.icon-certificate{
  width: 26rem;
  height: 25.8rem;
  margin-bottom: 8.2rem;
  background: url('../../../../public/asset/images/icon/ico_certificate.svg') no-repeat center;
}
.icon-right{
  vertical-align: baseline;
  width: 2.6rem;
  height: 2.6rem;
  padding-left: 5px;
  background-color: red;
}