@import '../../modules';

main.main-osp4117{
  div.width1440{
    padding: 9.6rem 0;
    address{
      span.addr-map{
        padding-bottom: 4rem;
        @include style_font(500, 2.4rem, 1, #212121);
        i.icon-circle-blue{
          width: 2rem;
          height: 2rem;
          margin-right: 2rem;
          background-color: chocolate;
        }
      }
      >p{
        padding: 0 0 2.2rem 4rem;
        @include style_font(400, 2rem, 1, #666666);
        span{
          width: 7.6rem;
          @include style_font(700, 2rem, 1, #666666);
        }
      }
    }

    iframe{
      margin-top: 1.8rem;
      width: 100%;
      height: 67.1rem;
    }
  }
}