// 🏠️ OSP-4112

@import '../../modules';

.width1352{
  width: 1352px;
  margin: 18rem auto;
}

main.main-osp4112{
  section.width1352{
    display: flex;
    padding: 5.8rem 7.5rem 13rem 8.7rem;
    box-shadow: 0px 30px 50px rgba(175, 175, 175, 0.1);
    border-radius: 50px;
    div{
      // position: relative;
      flex: 1 1 auto;
    }
    hgroup.img_tamxos_ceo{
      display: block;
      width: calc(428px + 5.5rem);
      padding-top: 11rem;
      height: 467px;
      background-color: aquamarine;
      // background-size: 428px 100%;
      // background: url('../../../../public/asset/images/img-OSP4112-ceo.png') right no-repeat;
      h3{
        position: relative;
        font-weight: 700;
        font-size: 36px;
        padding-bottom: 1.7rem;
        &::after{
          position: absolute;
          top: -49px;
          left: -30px;
          content: '';
          width: 30px;
          height: 30px;
          background: $color-2C99FF_azure;
          border-radius: 50%;
        }
      }h4{
        font-weight: 700;
        font-size: 18px;
      }
    }
    div.ceo-right{ 
      position: relative;
      margin-top: 2.5rem;
      margin-left: 8.4rem;
      &::after{
        position: absolute;
        content: '';
        bottom: -2rem;
        right: -4rem;

        width: 142px;
        height: 105px;
        // background: purple;
        background: #E9F3FF;
      }
      &::before{
        position: absolute;
        content: '';
        top: -7rem;
        left: -12rem;

        width: 142px;
        height: 105px;
        // background: purple;
        background: #E9F3FF;
        // background-blend-mode: multiply;
        mix-blend-mode: multiply;
      }
      p{
        line-height: 1.6em;
        font-weight: 300;
        font-size: 18px;
        color: #212121;
        padding-bottom: 1.6em;
      }

    }
  }

}