// 🏠️ OSP-4111

@import '../../modules';


.width1225{
  width: 1225px;
  margin: 0 auto;
}

main.main-osp4111{
  .width1440.banner-about-tmaxos{
    position: relative;
    padding-top: 12.3rem;
    padding-bottom: 32rem;
    h2{
      width: 44.6rem;
      padding-top: calc(30rem - 1em);
      margin-left: 10.8rem;
      font-weight: 700;
      font-size: 40px;
      line-height: 2em;
      strong{
        color: $color-2C99FF_azure;
      }
    }
  }
  .width1225.infor-about-tmaxos{
    .infor-list:first-child{
      p{
        padding: 0;
        font-size: 32px;
      }
    }
    .infor-list{
      position: relative;
      padding-top: 3.2rem;
      padding-bottom: 28rem;
      width: 100%;
      &::after{
        position: absolute;
        content: '';
        top: 0;
        width: 100%;
        height: 3px;
        background-color: $line_color-ECECEC_grey;
      }
      strong{
        display: inline-block;
        font-weight: 700;
        font-size: 24px;
        padding-bottom: 2rem;
        line-height: 150%;
      }
      ul{
        display: flex;
        justify-content: space-between;
        li{
          flex: 1 1 auto;
          p{
            color: #666666;
            font-size: 24px;
            line-height: 1.2em;
            padding-top: 0.8em;
          }
        }
      }
    }
    .infor-list:last-child{
      li{
        display: flex;
        align-items: flex-start;
        .cate-product{
          margin-top: 1em;
          display: inline-block;
          font-weight: 300;
          font-size: 18px;
          padding: 1.3rem 2rem;
          background: #666666;
          border-radius: 30px;
          color: white;
          letter-spacing: 0;
        }
        ul{
          display: block;
          padding-left: 1.5em;
        }
      }
    }
  }
}


// img
.img-about_tmaxos_banner{
  position: absolute;
  top: 12.3rem;
  // bottom: 0;  
  width: 100%;
  height: 38.8rem;
  background: rgba($color: #000000, $alpha: 0.6);
  z-index: -9;
}
