@import './../modules';

header{
  position: fixed;  
  width: 100%;
  z-index: 999;
  .gnb{
    display: flex;
    justify-content: space-between;
    align-items: stretch;
  }
  .gnb.width1440{
    height: 8.4rem;
  }
  a{
    position: relative;
    padding: 0.5em 0; 
    margin: 0 0.5em;
    transition: all 0.3s;
    height: 2rem;
    &::after{
      position: absolute;
      content: '';
      bottom: 0;
      right: 0;
      display: block;
      width: 100%;
      height: 2px;
      transform: scaleX(0);
      transform-origin: left;
    }
    &:hover{
      &::after{
        transform: scaleX(1);
        transition: all 0.3s
      }
    }
  }
  nav{
    ul{
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      height: 100%;
    }
    ul.gnb-sitemap{
      margin-left: 5.8rem;
      gap: 5.9rem;
      li:last-child{
        display: none;
      }
      li a{
        font-weight: 500;
        &:hover{
          font-weight: 700;
        }
      }
      .focused{
        @include style_font(700, 1.8rem, 1, $color-3855FF_ultramarine);
      }
    }
    ul.gnb-community{
      gap: 5.8rem;
      margin-right: 6.3rem;
      svg path{
        fill: #212121;
      }
      li a{
        font-weight: 500;
      }
      li:first-child a{
        &::after{
          background-color: $color-008CB2-commu-03;
        }
        &:hover{
          font-weight: 700;
          color: $color-008CB2-commu-03;
        }
      }
      li:last-child a{
        &::after{
          background-color: $color-3855FF_ultramarine;
        }
        &:hover{
          .icon-open_in_new svg path{
            transition: all 0.3s;
            fill: $color-3855FF_ultramarine;
          }
        }
      }
    }
    ul.gnb-user{
      gap: 2rem;
      a{
        @include style_font(400, 1.4rem, 1, #787878);
        margin: 0;
        &:hover{
          color: #666;
          font-weight: 500;
        }
      }
      .focused{
        font-weight: 500;
      }
    }
  }
}



.depth2{
  color: $font_color-white;
  background-color: $color-10319D_royalazure;
  .width1440{
    padding:76px 0 77px 0;

    display: flex;
    // justify-content: space-between;
    div{
      margin-right: 239px;

      h5{
        padding-left: 10px;
        font-weight: 700;
        font-size: 24px;
        padding-bottom: 0.5em;
      }
      p{
        width: 266px;

        padding-left: 10px;
        font-weight: 300;
        font-size: 18px;
        line-height: 1.8em;
        padding-bottom: 3em;
      }
      button{
        width: 208px;
        height: 47px;
        background: #FFFFFF;
        border-radius: 50px;
        font-weight: 700;

        color: $color-10319D_royalazure;
      }
    }
    ul{
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      li{
        width: 386px;
        padding-bottom: 1em;
        margin: 20px 48px 40px 0;
        
        font-weight: 700;
        border-bottom: 1px solid rgba(255, 255, 255, 0.07);
        a{
          color: $font_color-white;
        }
      }
    }
  }
}

.gnb-depth2{
  // display: none;
  width: 100%;
  position: fixed;
  display: block;
}
.depth2{
  display: none;
}
.depth2-open{
  display: block;
  @include animation(0s, 0.6s, fadeIn);

}
.depth2-close{
  @include animation(0s, 0.3s, fadeOut);
}
.depth2-none{
  display: none;
}
// main page와 sub page에 차이가 있다. 
header.header-main{
  // @include style_header($font_color-white, transparent);
  @include style_header("main");
}
header.header-sub{
  @include style_header("sub");
}