@import './../modules';

footer{
  background-color: $bg-main-news;
  color: $font_color-737373_grey;
  h4, a{
    color: $font_color-737373_grey;
  }
  div.footer-sitemap{
    border-bottom: 1px solid $line_color-E7E7E7_grey;
    ul.width1440{
      display: flex;
      justify-content: space-between;
      padding: 5.5rem 0 5.5rem 0;
      >li{
        font-size: 14px;
        width: 25rem;
        h4{
          padding-bottom: 2rem;
          font-weight: 700;
        }
        ul li a{
          display: inline-block;
          padding: 8px 0;
        }
        &:last-child{
          width: 10rem;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
        }
      }
    }
  }

  div.footer-information{
    padding: 4rem 0;

    display: flex;
    justify-content: space-between;
    div.--col1{
      width: 75%;
      font-size: 1.4rem;
      ul li{
        font-weight: 700;
        display: inline-block;
        padding: 1em 2em 1.6em 0;
      }
      address, small{
        font-size: inherit;
        padding-bottom: 0.8rem;
        line-height: 1.5em;
      }
    }
  }
}
